.wrapper-search-module {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;

  position: relative;
  max-width: 1136px;
  height: 90px;

  background: #ffffff;
  border-radius: 20px 20px 0px 0px;
}
.wrapper-search {
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 auto;
  margin-top: 20px;
}
.search-box {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 24px;
  padding-left: 64px;

  position: static;
  max-width: 642px;
  height: 50px;

  background: #f2f4f8;
  border-radius: 12px;
  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 0px 16px;

  outline: none;
  font-size: 20px;

  @media (max-width: 520px) {
    width: 90%;
  }
}
.search-btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 24px;

  position: static;
  width: 110px;
  height: 50px;

  background: #000000;
  border-radius: 12px;

  flex: none;
  order: 2;
  flex-grow: 0;
  margin: 0px 16px;
  font-size: 20px;
  transition: 0.3s;
  color: #ffffff;
  &:hover {
    background: #b82d44;
    cursor: pointer;
  }

  @media (max-width: 520px) {
    width: 90%;
    margin-top: 5px;
  }
}
.search-icon {
  position: absolute;
  top: 18px;
  left: 40px;
}
