.wrapper-cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 0 auto;

  max-width: 1136px;
  min-height: 480px;
  height: auto;

  background: #ffffff;
  border-radius: 0px 0px 20px 20px;
}
.card {
  transition: ease all 0.3s;
  width: 300px;
  border-radius: 20px 20px 20px 20px;
  cursor: pointer;
  a {
    text-decoration: none;
  }
}
.wrapper-card {
  border-radius: 20px;
  margin: 60px 10px 10px;
  padding-top: 300px;
  box-sizing: border-box;
  position: relative;
  box-shadow: 0 0 20px 10px rgba(25, 25, 25, 0.1);
  transition: ease all 0.3s;
  &:hover {
    transform: translateY(-10px);
    .card_img {
      height: 350px;
    }
  }
  .heart {
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 1;
    width: 25px;
    height: 25px;
    color: rgb(251, 251, 251);
    transition: all 0.3s;
    svg,
    path,
    circle {
      stroke: white;
      fill: white;
    }
    &:hover {
      cursor: pointer;
      svg,
      path,
      circle {
        stroke: red;
        fill: red;
      }
    }
  }
}
.card_img {
  background-size: contain;
  background-position: center bottom;
  background-repeat: no-repeat;
  position: absolute;
  bottom: calc(100% - 300px);
  width: 100%;
  height: 300px;
  transition: ease all 0.3s;
}
.card-info {
  display: block;
  padding: 20px 10px 5px 10px;
  box-sizing: border-box;
  font-family: 'Roboto';
  color: #666;
  h2 {
    font-size: 20px;
    text-transform: uppercase;
    text-align: center;
    font-weight: bold;
    line-height: 1.3;
    margin-bottom: 10px;
  }
  p {
    width: 100%;
    font-size: 14px;
    text-align: center;
  }
}
.action {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  box-sizing: border-box;
  margin-top: 15px;
}
.price-group {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  .price {
    font-size: 18px;
    color: #000;
    text-align: left;
    &.old-price {
      color: red;
      font-size: 15px;
      text-decoration: line-through;
      text-align: left;
    }
    &.new-price {
      color: #000;
      font-size: 25px;
      font-weight: 800;
    }
  }
}
.cart {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  position: relative;
  width: 35px;
  height: 35px;
  transition: all 0.3s;
  svg,
  path,
  circle {
    stroke: gray;
    fill: gray;
  }
  &:hover {
    cursor: pointer;
    svg,
    path,
    circle {
      stroke: #f3b713;
      fill: #f3b713;
    }
  }
}
.color_bg {
  border-radius: 20px 20px 0px 0px;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 300px;
  &.kakashi {
    background-color: #b82d44;
  }
  &.madara {
    background-color: #1d1d1d;
  }
  &.sasuke {
    background-color: #153a82;
  }
  &.nagato {
    background-color: #3f4a69;
  }
  &.jiraiya {
    background-color: #11a427;
  }
  &.naruto {
    background-color: #f3b713;
  }
}
